@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
.SelectCountDown {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.timerCountDown {
  padding-top: 60px;
  margin-top: 80px;
  width: 256px;
  height: 227px;
  background: url(/stopWatchReact/static/media/countdown.f6a5f7d0.svg);

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 30px;
}

.timerCountDown h1 {
  display: inline;
  font-family: "Share Tech Mono", monospace;
  color: #ffffff;
  font-size: 56px;
  font-weight: 400;
}
.timerCountDown p {
  display: inline;
  font-family: "Share Tech Mono", monospace;
  color: #ffffff;
  font-size: 24px;
  font-weight: 800;
  margin: 0;
}

.inputTime {
  display: flex;
  justify-content: space-between;
  width: 66%;
  margin-bottom: 30px;
}

.inputTime input {
  display: inline;
  width: 30px;
  border-radius: 5px;
  border: none;
  text-align: center;
  padding: 18px;
  margin: 5px;
  background-color: #e1e2e6;
  font-size: 24px;
}

.SelectCountDown div:last-child {
  width: 72%;
  display: flex;
  justify-content: space-between;
}
.SelectCountDown div:last-child button {
  font-size: 20px;
}

.btnfix {
  text-align: center;
  width: 120px;
}

.SelectPage_menu {
  margin-top: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.SelectPage_menu div {
  margin-top: 50px;
  width: 90%;
  display: flex;
  justify-content: space-around;
}

.SelectStopWatch {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.timerStopWatch {
  padding-top: 40px;
  margin-top: 130px;
  width: 234px;
  height: 221px;
  background: url(/stopWatchReact/static/media/stopwatch.1b608912.svg);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 30px;
}

.timerStopWatch h1 {
  display: inline;
  font-family: "Share Tech Mono", monospace;
  color: #8289a9;
  font-size: 56px;
  font-weight: 400;
}
.timerStopWatch p {
  display: inline;
  font-family: "Share Tech Mono", monospace;
  color: #8289a9;
  font-size: 24px;
  font-weight: 800;
  margin: 0;
}

.SelectStopWatch div:last-child {
  width: 75%;
  display: flex;
  justify-content: space-between;
}

.btnfix {
  text-align: center;
  width: 120px;
}
.tooltip {
  font-family: "Share Tech Mono", monospace;
  width: 100%;
  text-align: center;
  font-size: 24px;
  color: #8289a9;
  height: 74px;
  margin: 0;
  line-height: 3;
}

body {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: white;
  font-weight: bold;
}
button {
  background-color: #a8adc6;
  padding: 15px;
  border-radius: 10px;
  font-family: "Share Tech Mono", monospace;
  text-decoration: none;
  font-size: 24px;
  border: none;
  color: white;
}
button:hover {
  background-color: #8289a9;
  cursor: pointer;
}

.button {
  background-color: #a8adc6;
  padding: 15px;
  border-radius: 10px;
  font-family: "Share Tech Mono", monospace;
  text-decoration: none;
  font-size: 24px;
  border: none;
  color: white;
}
.button:hover {
  background-color: #8289a9;
  cursor: pointer;
}

.appScreen {
  width: 360px;
  height: 640px;
  background: #ffffff;
  box-shadow: 3px 3px 10px #00000010;
  border-radius: 15px;
  position: relative;
}

.monitor::after {
  margin-top: 15px;
  content: "Created by Dan, Hercules & Luiz";
  font-family: "Share Tech Mono", monospace;
  color: #8289a9;
  font-weight: 800;
}
.monitor {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background: #edeef0;
}

.voltar {
  position: absolute;
  right: 0;
  background: #a8adc6;
  color: #ffffff;
  padding: 10px;
  border-radius: 0 5px 0 5px;
  font-family: "Share Tech Mono", monospace;
  font-size: 20px;
  font-weight: 400;
}

.voltar:hover {
  background: #8289a9;
}

